// Media
@mixin media($size) {
  @if $size == lg {
    @media (max-width: 1199px) {
      @content; } }
  @else if $size == md {
    @media (max-width: 991px) {
      @content; } }
  @else if $size == sm {
    @media (max-width: 767px) {
      @content; } }
  @else if $size == xs {
    @media (max-width: 575px) {
      @content; } }
  @else {
    @media (max-width: $size) {
      @content; } } }
